.header {
  border-width: 1px;
  align-self: center;
  font-weight: bold; 
  color: rgb(48, 48, 48); 
  border-bottom: 1px solid #6d4b943b;
  padding-bottom: 3px;
  margin: '0';
}

.poll-answers {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 5px 0;
}

.option {
  color: rgb(48,48,48);
  border-color: rgb(48,48,48);
  font-weight: bold;
  border: 2px solid #000;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  padding: 10px;
  transition: all 0.3s ease;
  outline: 0;
  font-family: inherit;
  background-color: inherit;
}

.option:hover {
  background: #3030303b;
}

.answer-status {
  color: rgb(48, 48, 48);
  border-color: rgb(48, 48, 48);
  height: 42px;
  padding: 0;
  cursor: default;
  position: relative;
  font-weight: bold;
  border: 2px solid #000;
  border-radius: 5px;
  width: 100%;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  transition: all 0.3s ease;
  outline: 0;
  font-family: inherit;
  background-color: inherit;
}

.answer-fill {
  width: 100%;
  background-color: rgba(48, 48, 48, 0.23);
  height: 100%;
  border-radius: 3px;
  display: flex;
  align-items: center;
  transition: all 0.5s ease;
  box-sizing: border-box;
}

.answer-labels {
  position: absolute;
  top: calc(19px - 9px);
  margin: 0 10px;
  font-size: 16px;
}

.answer {
  color: rgb(48, 48, 48);
  margin-left: 15px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.answer-labels .vote::after {
  content: ' \2713';
}

.votes {
  text-align: inherit;
  margin: 0;
  padding: 0;
  font-size: 13px;
  color: #3030305e;
}